.toggle {
  position: relative;
  width: 37px;
  height: 37px; }
  .toggle::after {
    content: '';
    position: absolute;
    top: 12px;
    left: 50%;
    transform: translateX(-50%);
    width: 25px;
    height: 3px;
    border-radius: 2px;
    background: #1A212F;
    box-shadow: 0 6px #1A212F, 0 12px #1A212F; }
  .toggle--close::before {
    content: '';
    position: absolute;
    left: 6px;
    top: 50%;
    transform: rotate(-45deg);
    width: 25px;
    height: 3px;
    background: #1A212F;
    border-radius: 4px; }
  .toggle--close::after {
    box-shadow: none;
    left: 6px;
    top: 50%;
    transform: rotate(45deg);
    border-radius: 4px; }

.header {
