        display: block; } }

.nav a {
  cursor: pointer;
  transition: all 0.3s;
  text-shadow: 1px 1px 0 #fff, -1px -1px 0 #fff, -1px 1px 0 #fff, 1px -1px 0 #fff, 0 4px 4px rgba(0, 0, 0, 0.25); }
  .nav a:active {
