    color: #1A212F; }
    .btn--submit:hover {
      background: #FAC663; }
  .btn--reset {
    border: 2px solid #FAC663;
    background: #FFFEFD;
    color: #1A212F; }
    .btn--reset:hover {
      background: #FFFEFD; }

.main {
  padding-top: 95px; }

.sort {
  position: relative;
  width: 174px;
  padding: 11px 16px;
  background: #FFFEFD;
  border-radius: 6px;
  box-shadow: 0 4px 4px rgba(0, 0, 0, 0.25); }
  .sort--hidden {
    width: 0;
    box-shadow: none; }
    .sort--hidden .sort__list {
      display: none; }
  @media (max-width: 558px) {
    .sort {
      display: none; } }

.sort__item {
  margin-bottom: 16px; }
  .sort__item:last-child {
    margin-bottom: 0; }

.sort__toggler {
