  display: flex;
  align-items: center;
  line-height: 17px; }
  .sort__anchor::before {
    content: '';
    width: 7px;
    height: 12px;
    margin-right: 4px;
    background: url(../img/svg/sort-arrow.svg) no-repeat left;
    background-size: contain; }
  .sort__anchor--reverse::before {
    transform: rotate(180deg); }

.catalog {
  position: relative;
  padding-bottom: 30px; }

.catalog__wrapper {
  display: flex;
  justify-content: space-between;
  padding-top: 20px; }
  @media (max-width: 1120px) {
